import React from 'react';
import FAQItem from './FAQItem';
import './FAQ.css';

const FAQ = () => {
  const faqData = [
    { question: '  ?', answer: '          .' },
    { question: '  ?', answer: '    9:00  21:00.' },
    { question: '    ?', answer: ',       .' },
  ];

  return (

    <div className="faq-list">
      {faqData.map((item, index) => (
        <FAQItem key={index} question={item.question} answer={item.answer} />
      ))}
    </div>

  );
};

export default FAQ;
